import HyperList from "hyperlist";
import { saveAs } from 'file-saver';

FCBH.docReady(async () => {
  const recordingPage = new DatabaseRecordingsPage();
  await recordingPage.init();
});

class DatabaseRecordingsPage {
  fuseOptions = {
    shouldSort: true,
    threshold: 0.0,
    location: 0,
    distance: 100,
    maxPatternLength: 32,
    minMatchCharLength: 1,
    keys: ["lang_name", "country"],
  };
  results = [];
  fuse = null;
  searchText = "";
  filter = "All";
  sort = "language desc";
  viewDate = true;
  bibleRecordingsFile = 'bible_recordings';
  constructor() {}

  async init() {
    this.createSearchListener();
    this.avoidForm();
    this.processFiltering();
    this.processCheckbox();
    this.results = await this.fetchDatabase();
    this.initializeResultList(this.results);
    this.fuse = this.createSearcher(this.results);
    this.appendSorting();
    this.searchSortFilterAndRender(this.results);
    this.createDownloadListener();
  }

  /**
   * Gets the bible recording database
   */
  async fetchDatabase() {
    const jsonFile = window.location.href.toLowerCase().includes('bible-brain-available-content') 
      ? 'biblebrain_content'
      : this.bibleRecordingsFile;

    const { data } = await FCBH.axiosInstance.get(`${FCBH.BASE_URL}resources/${jsonFile}.json`);
    return data;
  }

  /**
   * Creates a searcher for the database
   * @param {any[]} results Result database
   */
  createSearcher(results) {
    const fuse = new Fuse(results, this.fuseOptions);
    return fuse;
  }

  /**
   * Search in the database
   * @param {String} searchString
   */
  search(searchString) {
    return this.fuse.search(searchString);
  }

  /**
   * Maps a search result to the raw data
   * @param {*} search
   */
  mapSearchToResults(search) {
    return search.map((result) => result.item);
  }

  /**
   * Applies filtering to the array
   * @param {String} filter
   * @param {Array} results
   */
  applyFilter(filter, results) {
    if (filter === "All") {
      return results;
    }
    return results.filter((result) => {
      switch (filter) {
        case "Video":
          return result.video_date && result.video_date !== "NULL";
        case "Text":
          return result.text_date && result.text_date !== "NULL";
        case "Audio":
          return result.audio_date && result.audio_date !== "NULL";
        default:
          return false;
      }
    });
  }

  /**
   * Executes sorting
   * @param {Array} results
   */
  sorting(results = []) {
    if (!this.sort) {
      return results;
    }

    const [column, type] = this.sort.split(" ");
    const key = this.columnToKey(column);
    const emptyRows = results.filter((result) => !result[key] || result[key] === "NULL");
    const sortedRows = results
      .filter((result) => result[key] && result[key] !== "NULL")
      .sort((a, b) => {
        if (type === "desc") {
          return a[key] > b[key] ? 1 : -1;
        }
        return a[key] > b[key] ? -1 : 1;
      });
    return [...sortedRows, ...emptyRows];
  }

  /**
   * Maps a column title to the corresponding "results" data structure key
   * @param {*} column
   */
  columnToKey(column) {
    const columns = { 
      language: "lang_name",
      country: "country",
      video: "video_date",
      text: "text_date",
      audio: "audio_date",
      iso: "iso",
    };

    return columns[column] ?? columns.language;
  }

  /**
   * Creates the search listener for the searching bar
   */
  createSearchListener() {
    const searchElement = document.querySelector("#Search");
    searchElement.addEventListener("keyup", (ev) => {
      this.searchText = ev.target.value;
      this.searchSortFilterAndRender();
    });
  }

  /**
   * Avoids form submission so webflow form success is not displayed
   */
  avoidForm() {
    const form = document.querySelector("#wf-form-MP3-Downloads");
    form.addEventListener("submit", (ev) => {
      ev.preventDefault();
      ev.stopPropagation();
      ev.stopImmediatePropagation();
    });
  }

  /**
   * Hanldes the Format filter
   */
  processFiltering() {
    const selectedFilter = document.querySelector("#format > div > div");
    const filterListContainer = document.querySelector("#format nav");
    while (filterListContainer?.lastChild) {
      filterListContainer.removeChild(filterListContainer.lastChild);
    }
    const options = ["All", "Audio", "Video", "Text"];
    const nodes = options.map((option) => {
      const node = document.createElement("a");
      node.href = "#";
      node.classList.add("dropdown-link", "w-dropdown-link");
      node.textContent = option;
      node.setAttribute("data-value", option);
      const actionFn = () => {
        selectedFilter.textContent = node.getAttribute("data-value");
        selectedFilter.dispatchEvent(
          new MouseEvent("mouseup", {
            view: window,
            bubbles: true,
            cancelable: false,
          })
        );
        selectedFilter.dispatchEvent(
          new MouseEvent("click", {
            view: window,
            bubbles: true,
            cancelable: false,
          })
        );
        this.filter = node.getAttribute("data-value");
        this.searchSortFilterAndRender();
      }
      node.addEventListener("touchstart", actionFn)
      node.addEventListener("click", actionFn);
      return node;
    });
    filterListContainer.append(...nodes);
    selectedFilter.textContent = "All";
  }

  /**
   * Handles the display date switch
   */
  processCheckbox() {
    // Fixes bug that makes clicking inside the modal execute change of the checkbox
    const modal = document.querySelector(".recordings-database__date-toggle .modal-window");
    modal.addEventListener("click", (ev) => {
      if (ev.path.includes(modal.querySelector(".modal__close"))) {
        document.querySelector(".modal-overlay").dispatchEvent(
          new MouseEvent("click", {
            view: window,
            bubbles: true,
            cancelable: false,
          })
        );
      }
      ev.preventDefault();
      ev.stopPropagation();
      ev.stopImmediatePropagation();
    });
    const checkbox = document.querySelector("input[name='View-Date']");
    checkbox.checked = true;
    checkbox.addEventListener("change", () => {
      this.viewDate = checkbox.checked;
      document.querySelectorAll('div[data-attr="dateNode"]').forEach((node) => {
        node.querySelector("span").style.display = checkbox.checked ? "inline" : "none";
        node.querySelector("div").style.display = checkbox.checked ? "none" : "block";
      });
    });
  }

  /**
   * Pretty much the title
   */
  searchSortFilterAndRender() {
    if (!this.searchText) {
      this.renderList(this.sorting(this.applyFilter(this.filter, this.results)));
      return;
    }

    const results = this.sorting(this.applyFilter(this.filter, this.mapSearchToResults(this.search(this.searchText))));
    this.renderList(results);
  }

  /**
   * Initialize the table
   * @param {Array} results
   */
  initializeResultList(results = []) {
    this.renderContainer = document.querySelector(".default-width-container.db-container");
    while (this.renderContainer.lastChild) {
      this.renderContainer.removeChild(this.renderContainer.lastChild);
    }
    this.list = HyperList.create(this.renderContainer, {
      itemHeight: 100,
      total: results.length,
      generate(index) {
        const el = document.createElement("div");
        el.innerHTML = `ITEM ${index + 1}`;
        return el;
      },
    });
  }

  /**
   * Obtains the element height by rendering on the dom.
   * Unefficient but yelds the best results.
   * @param {Element} node
   */
  getNodeHeight(node) {
    let height,
      clone = node.cloneNode(true);
    // hide the meassured (cloned) element
    clone.style.cssText = "position:fixed; top:-9999px; opacity:0;";
    // add the clone to the DOM
    document.body.appendChild(clone);
    // meassure it
    height = clone.clientHeight;
    // cleaup
    clone.parentNode.removeChild(clone);
    return height;
  }

  /**
   * Renders a list of results in screen.
   * @param {Array} results
   */
  renderList(results) {
    this.renderContainer.style.minHeight = "800px";
    const config = {
      // All items must be the exact same height currently. Although since there is
      // a generate method, in the future this should be configurable.
      itemHeight: 100,

      // Specify the total amount of items to render the virtual height.
      total: results.length,

      // Wire up the data to the index. The index is then mapped to a Y position
      // in the container.
      generate: (index) => {
        const rowDiv = document.createElement("div");
        rowDiv.id = "db-table-container";
        rowDiv.classList.add("w-layout-grid", "recordings-db-grid");
        rowDiv.style.width = "100%";
        rowDiv.append(...this.createRow(results[index], index % 2 === 0));
        return { element: rowDiv, height: this.getNodeHeight(rowDiv) };
      },
    };
    this.list.refresh(this.renderContainer, config);
  }

  /**
   * Creats a new row for the table
   * @param {any} result
   * @param {boolean} isEven
   */
  createRow(result, isEven) {
    const languageNode = this.createLanguageNode(result);
    const countryNode = this.createSimpleNode(result.country);
    countryNode.classList.add("recordings-db-grid-text--country");
    const audioNode = this.createDateNode(result.audio_date);
    const videoNode = this.createDateNode(result.video_date);
    const textNode = this.createDateNode(result.text_date);
    const isoNode = this.createSimpleNode(result.iso.toUpperCase());
    const row = [languageNode, countryNode, audioNode, videoNode, textNode, isoNode];
    if (!isEven) {
      row.forEach((node) => {
        node.classList.add("recordings-db-grid-text--even");
      });
    }
    return row;
  }

  /**
   * Creates a link grid node with the language
   * @param {*} result
   */
  createLanguageNode({ bible_id, lang_name, bible_name, country }) {
    const link = document.createElement("a");
    link.href = "https://live.bible.is/bible/" + bible_id;
    link.target = "_blank";
    link.title = `${lang_name} | ${bible_name}`;
    link.textContent = `${lang_name} `;
    link.classList.add("txt-sm", "txt-sm--link", "recordings-link");

    const textNode = this.makeTextNode(bible_name);
    textNode.classList.add("language-version");

    const countryMobile = this.makeTextNode(country);
    countryMobile.classList.add("txt-bold", "country-mobile");

    const container = this.makeContainer();

    container.append(link, textNode, countryMobile);
    return container;
  }

  /**
   * Creates a simple text content grid node
   * @param {String} content
   */
  createSimpleNode(content) {
    const textNode = this.makeTextNode(content);

    const container = this.makeContainer();

    container.append(textNode);
    return container;
  }

  /**
   * Creates a date node with a checkmark
   * @param {String} content
   */
  createDateNode(content) {
    const container = this.makeContainer();
    if (!content || content === "NULL") {
      return container;
    }
    const textNode = this.makeTextNode(content);
    textNode.style.display = this.viewDate ? "none" : "block";
    const checkImgSrc = `
    <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
	 width="32px" height="32px" viewBox="0 0 78.369 78.369" style="enable-background:new 0 0 78.369 78.369;fill:#20b2aa"
	 xml:space="preserve">
      <g>
        <path d="M78.049,19.015L29.458,67.606c-0.428,0.428-1.121,0.428-1.548,0L0.32,40.015c-0.427-0.426-0.427-1.119,0-1.547l6.704-6.704
          c0.428-0.427,1.121-0.427,1.548,0l20.113,20.112l41.113-41.113c0.429-0.427,1.12-0.427,1.548,0l6.703,6.704
          C78.477,17.894,78.477,18.586,78.049,19.015z"/>
      </g>
    </svg>
`;
    const span = document.createElement("span");
    span.innerHTML = checkImgSrc;
    span.style.display = this.viewDate ? "inline" : "none";

    container.append(textNode, span);
    container.setAttribute("data-attr", "dateNode");
    return container;
  }

  /**
   * Creates a basic text node
   * @param {string} content
   */
  makeTextNode(content) {
    const textNode = document.createElement("div");
    textNode.classList.add("txt-xs");
    textNode.textContent = content;
    return textNode;
  }

  /**
   * Creates a column container
   */
  makeContainer() {
    const container = document.createElement("div");
    container.classList.add("recordings-db-grid-text");
    return container;
  }

  /**
   * Adds sorting to table
   */
  appendSorting() {
    const titles = document.querySelectorAll(".recordings-db-grid-title");
    for (const title of titles) {
      const text = title.querySelector("h5").textContent;
      if (text.toLowerCase() === "language") {
        title.classList.add("recordings-db-grid-title--is-active");
      }
      title.addEventListener("click", () => {
        this.onSortClicked(text, title, titles);
      });
    }
  }

  /**
   * Enable/disable active sorting
   * @param {string} text
   * @param {Element} element
   * @param {NodeListOf<Element>} titles
   */
  onSortClicked(text, element, titles) {
    const ACTIVE_SORTING_CLASS = "recordings-db-grid-title--is-active";
    const ACTIVE_TOGGLE_CLASS = "recordings-db-grid-title--is-active-toggle";

    if (element.classList.contains(ACTIVE_SORTING_CLASS)) {
      element.classList.toggle(ACTIVE_TOGGLE_CLASS);
    }
    element.classList.add(ACTIVE_SORTING_CLASS);

    const sortType = element.classList.contains(ACTIVE_TOGGLE_CLASS) ? "asc" : "desc";

    for (const title of titles) {
      if (title !== element) {
        title.classList.remove(ACTIVE_SORTING_CLASS, ACTIVE_TOGGLE_CLASS);
      }
    }

    this.sort = `${text.toLowerCase()} ${sortType}`;
    this.searchSortFilterAndRender();
  }
  createDownloadListener(){
    const downloadButton = document.getElementById('recordingsDownload');
    downloadButton.addEventListener('click', this.downloadCSVFile.bind(this));
  }
  downloadCSVFile(){
    const fileName = `${this.bibleRecordingsFile}.csv`;
    saveAs(`${FCBH.BASE_URL}resources/${fileName}`, fileName);
  }
}
